@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.0/normalize.min.css);@import url(https://cdnjs.cloudflare.com/ajax/libs/bulma/0.7.1/css/bulma.min.css);@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,500,500i,700,700i);
* {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
header {
  height: 60px;
  box-shadow: 0px 0px 15px #F0F0F0;
}
header nav {
    height: 60px;
}
header nav .container {
      height: 100%;
}
header .navbar-brand {
    height: 100%;
}
header .navbar-brand #header-nav-logo {
      display: flex;
      align-items: center;
      font-weight: bold;
      font-size: 32px;
      padding-left: 15px;
      padding-right: 15px;
      border-radius: 0;
      height: 100%;
}
header .navbar-brand .navbar-burger {
      align-items: center;
      height: 100%;
}
header .navbar-menu .navbar-end {
    display: grid;
    grid-auto-flow: column;
    align-items: center;
    font-weight: 500;
    height: 100%;
}
header .navbar-menu .navbar-end :last-child {
      margin-left: 5px;
}
@media only screen and (max-width: 1100px) {
#header-nav-menu * {
    text-align: center;
}
}
#footer-container {
  padding: 15px 10px 15px 10px;
  font-size: 12px;
  color: #A9A9A9;
}
#footer-container a {
    text-decoration: none;
    color: #A9A9A9;
}
